<template>
	<view class="container">
		<scroll-view scroll-y="true">
			<view class="title"><text>选择售后类型</text></view>
			<view class="title-hint" v-if="titleHintShow"><text>不同物流状态的宝贝不能一起退款哦～</text></view>

			<view class="type_chose">
				<view class="chose-title">请选择售后类型:</view>
				<!-- 根据订单状态显示各种按钮 -->
				<view class="type_btn_group">
					<radio-group @change="typeRadioChange">
						<label v-if="buttonIsShow[0]" class="radio">
							<radio value="0" :disabled="!buttonIsAble[0]" />仅退款
						</label>
						<label v-if="buttonIsShow[1]" class="radio">
							<radio value="1" :disabled="!buttonIsAble[1]" />退货退款
						</label>
						<label v-if="buttonIsShow[2]" class="radio">
							<radio value="2" :disabled="!buttonIsAble[2]" />换货
						</label>
					</radio-group>
					<view v-if="chosedAftersaleType===2" class="type-hint">
						<text>当前售后类型仅支持选择单个商品</text>
					</view>
				</view>
			</view>
			<view class="prod-list">
				<block v-for="(prod, index) in orderItems" :key="index">
					<view class="item">
						<view class="btn">
							<label>
								<checkbox @tap="onSelectedOrderItem" :data-index="index" :value="toString(prod.prodId)"
									:checked="prod.checked" :disabled="prod.disabled" color="#105c3e"></checkbox>
							</label>
						</view>
						<view class="prod-info">
							<view class="pic">
								<image :src="prod.pic"></image>
								<text
									class="prod-status">{{ prod.deliveryStatus === 0 ? "未发货": prod.deliveryStatus === 1 ?"已发货未签收":"已发货已签收"}}</text>
							</view>
							<view class="opt">
								<view class="prod-name">{{ prod.prodName }}</view>
								<text
									:class="'prod-info-text ' + (prod.skuName ? '' : 'empty-n')">{{ prod.skuName }}</text>

								<view class="properties">
									<text class="color">颜色:{{ prod.color }}</text>
									<text class="size">尺码:{{ prod.size }}</text>
								</view>
								<view class="price-count">
									<text class="count-symbol">数量</text>
									<text class="count-num">{{ prod.prodCount }}</text>
									<text class="price-symbol">¥</text>
									<text class="price-num">{{ prod.price }}</text>
								</view>
								<view class="prod-hint" v-if="(chosedAftersaleType === 0 &&
          prod.deliveryStatus === 2)||(chosedAftersaleType > 0 && prod.deliveryStatus === 0)">
									<text>该宝贝不支持当前售后类型</text>
								</view>
							</view>
						</view>
					</view>
				</block>
			</view>
		</scroll-view>
		<button type="primary" @click="toReasonChosePage" class="next-step">下一步</button>

	</view>
</template>
<script>
	var http = require("../../utils/http.js");

	export default {
		data() {
			return {
				titleHintShow: false,
				chosedAftersaleType: -1,
				orderItems: [],
				buttonIsShow: [false, false, false],
				buttonIsAble: [true, true, true],
				isConfirmReceive: false,
				chosedDeliveryStatus: -1,
				chosedOrderItemsNum: 0,
			};
		},
		onLoad: function(options) {
			this.setData({
				orderItems: options.orderItems.filter(orderItem => orderItem.aftersaleStatus === 0 || orderItem
					.aftersaleStatus >= 300),
			}); // 加载商品信息
			for (var orderItem in orderItems) {
				orderItem.checked = false;
				orderItem.disabled = false;
			}
			this.checkHintIsShow();
			this.checkButtonIsShow();

		},

		methods: {
			typeRadioChange: function(evt) {
				this.chosedAftersaleType = parseInt(evt.detail.value);
				this.checkOrderItemIsAble();
			},
			onSelectedOrderItem: function(e) {
				var index = e.currentTarget.dataset.index; // 获取data- 传进来的index

				var orderItems = this.orderItems; // 获取购物车列表

				if (orderItems[index].disabled) {
					return;
				}

				var checked = orderItems[index].checked; // 获取当前商品的选中状态


				checked == true ? this.chosedOrderItemsNum-- : this.chosedOrderItemsNum++;
				if (checked === false) {
					this.chosedDeliveryStatus = orderItems[index].deliveryStatus;
				} else if (checked === true && this.chosedOrderItemsNum === 0) {
					this.chosedDeliveryStatus = -1;
				}

				orderItems[index].checked = !checked; // 改变状态

				this.setData({
					orderItems: orderItems,
				});
				this.checkButtonIsAble(); //改变上方按钮状态
				this.checkOrderItemIsAble(); //改变其它单选框状态
			},

			checkHintIsShow: function() {
				for (var i = 1; i < this.orderItems.length; i++) {
					if (this.orderItems[i].deliveryStatus != this.orderItems[i - 1].deliveryStatus) {
						this.titleHintShow = true;
						return;
					}
				}
			},

			checkButtonIsShow: function() {
				//0. 确认收货了,只显示退货退款和换货,否则走1,2,3
				//1. 全是未发货(0),不显示
				//2. 不仅有已发货已签收(2)(已发货未签收支持所有,未发货支持退款),就全显示
				//3. 只有已发货已签收(2),就不显示仅退款
				var buttonIsShow = this.buttonIsShow;

				if (this.isConfirmReceive) {
					buttonIsShow[1] = true;
					buttonIsShow[2] = true;
				} else {
					console.log("未收货");

					for (var i = 0; i < this.orderItems.length; i++) {
						var orderItem = this.orderItems[i];
						if (orderItem.deliveryStatus === 1) {
							buttonIsShow[0] = true;
							buttonIsShow[1] = true;
							buttonIsShow[2] = true;
							console.log("发货未签收");
							break;
						} else if (orderItem.deliveryStatus === 2) {
							buttonIsShow[1] = true;
							buttonIsShow[2] = true;
						} else {
							buttonIsShow[0] = true;
						}
					}
				}
				if (

					buttonIsShow[0] === true &&
					buttonIsShow[1] === false &&
					buttonIsShow[2] === false

				) {
					buttonIsShow[0] = false;
					this.chosedAftersaleType = 0;
					console.log("仅退款");
				}
				this.setData({
					buttonIsShow: buttonIsShow,
				});

			},

			checkButtonIsAble: function() {
				//0.所有商品都未选中时,按钮全部有效
				//1. 只有相同物流状态商品可同时选中
				//2. 可以用个单变量来确定当前选中的是哪种物流状态

				//3. 全是未发货商品, 只显示仅退款
				//4. 全是已发货未签收商品, 都显示
				//5. 全是已发货已签收商品, 不显示仅退款
				//6. 选中一个商品时才能点换货
				var buttonIsAble = this.buttonIsAble;

				if (this.chosedDeliveryStatus === -1) {
					buttonIsAble[0] = true;
					buttonIsAble[1] = true;
					buttonIsAble[2] = true;
				} else if (this.chosedDeliveryStatus === 0) {
					buttonIsAble[0] = true;
					buttonIsAble[1] = false;
					buttonIsAble[2] = false;
				} else if (this.chosedDeliveryStatus === 1) {
					buttonIsAble[0] = true;
					buttonIsAble[1] = true;
					buttonIsAble[2] = true;
				} else if (this.chosedDeliveryStatus === 2) {
					buttonIsAble[0] = false;
					buttonIsAble[1] = true;
					buttonIsAble[2] = true;
				}

				if (this.chosedOrderItemsNum > 1) {
					buttonIsAble[2] = false;
				}
				this.setData({
					buttonIsAble: buttonIsAble,
				});
			},
			checkOrderItemIsAble: function() {
				var orderItems = this.orderItems;

				//0. 受类型按钮和其它radio共同影响
				//1. 某种发货状态的radio被选中,不同类型radio禁用(该规则优先)
				//2. 仅退款按钮被选中时,已发货已签收商品不能被选中
				//3. 退货退款和换货按钮选中,未发货商品不能被选中
				//4. 所有按钮都没选中,且所有radio也没选中,所有radio一定不禁用
				for (var i = 0; i < orderItems.length; i++) {
					var orderItem = orderItems[i];
					if (
						this.chosedDeliveryStatus != -1 &&
						orderItem.deliveryStatus != this.chosedDeliveryStatus
					) {
						//规则1
						orderItem.disabled = true;
					} else if (
						this.chosedAftersaleType === 0 &&
						orderItem.deliveryStatus === 2
					) {
						orderItem.disabled = true;
					} else if (this.chosedAftersaleType > 0 && orderItem.deliveryStatus === 0) {
						orderItem.disabled = true;
					} else {
						orderItem.disabled = false;
					}
				}
				this.setData({
					orderItems: orderItems,
				});
			},

			toReasonChosePage: function() {
				var orderItems = this.orderItems.filter(orderItem => orderItem.checked === true && orderItem
					.disabled === false);
					
					
				var aftersaleType = chosedAftersaleType;
				
				if (orderItems.length === 0  ) {
					uni.showToast({
						title: '请选择商品'
					});
					return;
				}
				
				if (chosedAftersaleType = -1) {
					uni.showToast({
						title: '请选择售后类型'
					});
					return;
				}
				uni.setStorageSync("aftersaleOrderItems", JSON.stringify(this.orderItems));
				uni.navigateTo({
					url: "/pages/aftersale-apply/aftersale-reason-chose?aftersaleType=" + aftersaleType,
				});
			},
		},
	};
</script>

<style lang="less">
	page {
		background-color: #f4f4f4;
	}

	.container {
		height: 100vh;
		padding-bottom: 100rpx;

		scroll-view {
			height: 100%;
		}

		.title {
			font-weight: 800;
			text-align: center;
			margin-top: 40rpx;
			margin-bottom: 40rpx;
		}

		.title-hint {
			background: #fff;
			padding: 20rpx;
			margin-bottom: 20rpx;
			font-size: 25rpx;
			color: orangered;
		}

		.type_chose {
			.chose-title {
				font-weight: 800;
				padding-bottom: 20rpx;
			}

			background: #fff;

			.type_btn_group {

				.radio {
					padding-left: 50rpx;
					font-size: 28rpx;
				}
			}

			.type-hint {
				font-size: 25rpx;
				margin-top: 20rpx;
				color: orangered;
			}

			padding: 20rpx;
			margin-bottom: 20rpx;
		}

		.prod-list {
			.item {
				background: #fff;
				display: flex;
				align-items: center;
				padding: 20rpx;

				.prod-info {
					display: flex;
					margin-left: 20rpx;

					.pic {
						position: relative;
						text-align: center;
						width: 150rpx;
						height: 150rpx;

						image {
							max-width: 100%;
							max-height: 100%;
							border-radius: 8rpx;
							vertical-align: middle;
						}

						.prod-status {
							position: absolute;
							bottom: 0;
							left: 0;
							width: 100%;
							font-size: 25rpx;
							background-color: rgba(0, 0, 0, 0.5);
							color: #fff;
							border-radius: 8rpx;
						}
					}

					.opt {
						font-size: 28rpx;
						line-height: 180%;
						margin-left: 30rpx;

						.properties {
							color: grey;

							.size {
								margin-left: 30rpx;
							}
						}

						.price-count {
							.price-symbol {
								margin-left: 30rpx;
							}
						}

						.prod-hint {
							font-size: 25rpx;
							color: orangered
						}
					}
				}
			}

			.spacer {
				width: 100%;
				height: 500rpx;
			}
		}

		.next-step {
			position: absolute;
			bottom: 0;
			width: 100%;
		}
	}
</style>